@import "../../../styles/placeholders.scss";
@import "../../../styles/mixins.scss";
@import "../../../styles/vars.scss";

// @import "../../../images/blog-article/mark.svg";

.article {
	&__container {
		@extend %flex-center;
		flex-direction: column;

		& * {
			max-width: 410px;

			@media screen and (min-width: 1290px) {
				max-width: 1070px;
			}
		}
	}

	&__align {
		@media screen and (min-width: 1290px) {
			display: flex;
			justify-content: flex-start;
			width: 100%;
		}
	}

	&__title {
		@include font($dark-color, 1.33, 30px, 600);
		@media screen and (min-width: 1290px) {
			font-size: 40px;
		}
	}

	&__img {
		margin: 30px 0 40px;
		width: 100%;
		max-width: 410px;
		margin-left: auto;
		margin-right: auto;

		@media screen and (min-width: 1290px) {
			margin: 40px auto 40px;
			max-width: 1070px;
		}
	}

	&__descr {
		@include font($dark-color, 1.87, 16px, 400);
		text-align: start;
	}

	&__subtitle {
		margin: 40px 0 20px;
		@include font($dark-color, 1.4, 20px, 600);

		@media screen and (min-width: 1290px) {
			margin: 40px 0 30px;
		}
	}

	&__list {
		display: flex;
		flex-direction: column;
		gap: 20px;
	}

	&__item {
		display: flex;
		gap: 15px;

		@media screen and (min-width: 1290px) {
			justify-content: center;
		}

		&::before {
			content: "";

			display: block;
			@include size(32px, 32px);

			background-image: url("../../../images/blog-article/mark.svg");
		}
	}

	&__paragraph {
		width: 80%;
		@include font($dark-color, 1.87, 16px, 400);

		@media screen and (min-width: 480px) {
			max-width: 363px;
		}

		@media screen and (min-width: 1290px) {
			max-width: 1023px;
		}
	}

	&__text {
		margin: 40px 0;
		@include font($dark-color, 1.87, 16px, 400);
	}

	&__buttons {
		@extend %flex-center;
		flex-direction: column-reverse;
		gap: 30px;

		@media screen and (min-width: 1290px) {
			flex-direction: row;
			gap: 446px;
		}
	}

	&__button {
		&:nth-child(1) > .article__btn {
			padding: 13px 51px;
		}

		&:nth-child(2) > .article__btn {
			padding: 13px 60px;
		}
	}

	&__btn {
		@extend %flex-center2;
		width: 100%;
		gap: 15px;
		border-radius: 8px;

		background-color: $yellow-color;

		transition: background-color 250ms ease-in;

		&:hover,
		&:focus {
			background-color: #f3ab3f;
		}

		@media screen and (min-width: 480px) {
			max-width: 402px;
		}

		& > span {
			text-align: center;
			@include font(#fff, 1.26, 19px, 600);
		}
	}

	&__icon {
		@include size(24px, 24px);
		fill: #fff;

		&--right {
			transform: rotate(180deg);
		}
	}
}
